.StoryBox{
    position:relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height:200px;
    box-shadow: 0px 5px 17px -7px rgba(0,0,0,0.75);
    border-radius: 20px;
    margin-right: 10px;
    transform:transform 100ms ease-in;
    cursor: pointer;
}
.StoryBox:hover{
    transform:scale(1.07)
}

.Images__app >div{
    display: grid;
    align-content: center;
    justify-content: center;
    /* grid-template-columns: repeat(10,1fr); */
    grid-template-columns: repeat(10,2fr);
    grid-gap: 10px;
    gap: 10px;;
}
.Images__app{
  display: grid;
  justify-content: center;
  align-content: center;
  width: 100vw;

}
.Image__app__img{
    /* max-height: 100vh; */
    min-width: 80vw;
    /* width: 100%;
    height: 100%; */
    height: 100vh;
    background-position: center center;
    background-size: cover;

    box-shadow: 0px 5px 17px -7px rgba(0,0,0,0.75);
    border-radius: 50px;
    cursor: pointer;
    justify-self: center;
    align-self: center;
    align-items: center;
    justify-items: center;
    transition: transform 1.5s;
    transform: scale(.65);

}
.Image__app__img:hover{
    transform: scale(.9);

}
@media screen and (min-width: 320px) {
    .Images__app >div{
    grid-template-columns: repeat(3,1fr);
    }
}
@media screen and (min-width: 480px) {
    .Images__app >div{
    grid-template-columns: repeat(4,1fr);
    }
}
@media screen and (min-width: 600px) {
    .Images__app >div{
    grid-template-columns: repeat(5,1fr);
    }
}
@media screen and (min-width: 768px) {
    .Images__app >div{
    grid-template-columns: repeat(6,1fr);
    }
}
@media screen and (min-width: 900px) {
    .Images__app >div{
    grid-template-columns: repeat(7,1fr);
    }
}
@media screen and (min-width: 1024px) {
    .Images__app >div{
    grid-template-columns: repeat(8,1fr);
    }
}
@media screen and (min-width: 1200px) {
    .Images__app >div{
    grid-template-columns: repeat(9,1fr);
    }
}
.AlbumLargImgs{
    display: grid;
    justify-content: center;
    align-content: center;
}

.AlbumLargImgs >img{
    max-height: 80vh;
    max-width: 100vw;
    box-shadow: 0px 5px 17px -7px rgba(0,0,0,0.75);
    border-radius: 50px;
    cursor: pointer;
}
/* progress bar styles */
.progress-bar{
    height: 5px;
    background: #8b8383;
    margin-top: 20px;
  }
.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color:  #ff4a4a;
}
.UploadForm__app{
  height: 10vh;
}

.UplodeForom__input{
    display: none;
}

.UploadForm__input__icon{
  display: grid;
  color: white;
  background-color: black;
  width: 70px;
  height: 70px;
  border: 5px solid grey;
  border-radius: 30px;
  margin:  auto;
  /* justify-items: center; */
  align-content: center;
  justify-content: center;
  transition: 1s;
}
.UploadForm__input__icon:hover{
  color:black;
  background-color:white;
  margin-bottom: 10px;
}
.UploadForm__error{
  margin-top: 10px;
  text-align: center;
}
.UploadForm__filename{
  text-align: center;
}
.Album__title__left{
  justify-items: center;
  align-content: center;
  align-self: center;
}
